import React from 'react';

import "./Desktop.scss";

import DesktopIcon from './DesktopIcon/DesktopIcon';

const Desktop = () => {
    const iconsContext = require.context('../../assets/images/icons', true);

    const desktopIcons = [
        {
            type: "animated",
            icon: "Folder",
            label: "Projects",
            hoverAnimation: true
        },
        {
            type: "animated",
            icon: "Resume",
            label: "Resume",
            autoplay: true,
            loop: false,
            hoverAnimation: false
        },
        {
            type: "animated",
            icon: "Folder",
            label: "Other Stuff",
            hoverAnimation: true
        }
    ];

    return (
        <div className="desktop">
            <div className="desktop__icons">
                {desktopIcons.map((icon, index) => <DesktopIcon {...icon} key={index} uniqueIdentifier={index} />)}
            </div>
        </div>
    )
};

export default Desktop;