import React, { createContext, useEffect } from 'react';
import './App.scss';

import {TimelineMax, Expo} from 'gsap';

import TopBar from './components/TopBar/TopBar';
import Desktop from './components/Desktop/Desktop';
import ParallaxBackground from './components/ParallaxBackground/ParallaxBackground';
import Worm from './components/Worm/Worm';
import Window from './components/Window/Window';

const email =  "koreh.andrei20@gmail.com";
export const EmailContext: React.Context<string> = createContext(email);

const App = () => {

  useEffect(()=>{
    setTimeout(()=>{
      const parallaxBackground =  document.querySelector(".parallax");
      const topBar = document.querySelector(".topBar");
      if(parallaxBackground && topBar){
        const loadingTimeline = new TimelineMax();
        loadingTimeline
          .to(parallaxBackground, 3, {scrollTo: {y: 0, x: 0}})
          .to(topBar, 1, {y: 0, ease: Expo.easeOut}, "-=.5");
      }
    }, 500)
  },[])

  return (
    <div className="App">
      <Worm />
      <EmailContext.Provider value={email}>
        <TopBar />
        <Desktop />
      </EmailContext.Provider>
      <ParallaxBackground />
    </div>
  );
}

export default App;
