import React, {useEffect} from 'react';
import lottie from 'lottie-web';

import "./Worm.scss";
import {TimelineLite } from 'gsap';

const Worm = () => {

    useEffect(()=>{
        const wormContainer =  document.querySelector(`#worm`);
        const animation = lottie.loadAnimation({
            container: wormContainer,
            renderer: "svg",
            loop: true,
            autoplay: true,
            path: `/assets/animations/Worm.json`
        });

        const parallaxCover = document.querySelector(".parallax");

        if(wormContainer && parallaxCover){
            const timeline = new TimelineLite({
                onComplete: function() {
                    (this as TimelineLite).restart();
                }
            });

            const wormSpeed =  160;
            const wallOffset = "-44px";

            timeline.set(wormContainer, {rotation: 90, left: wallOffset, right: "initil"});
            timeline.to(wormContainer, wormSpeed, {y: parallaxCover.clientHeight});
            timeline.set(wormContainer, {rotation: -90, left: "initial", right: wallOffset});
            timeline.to(wormContainer, wormSpeed, {y: "-50px"});
        }

    }, []);


    return (
        <div id="worm"></div>
    );
}

export default Worm;